@import "spectre/variables";
@import "spectre/mixins.scss";

li {
    list-style: none;
}

.btn-secondary {
    @include button-variant($gray-color-light);
}

.btn-warning {
    @include button-variant($warning-color);
}

.btn-info {
    @include button-variant($info-color);
}

.btn-secondary, .btn-secondary:hover, .btn-warning, .btn-warning:hover {
    color: #fff;
}

.rd-content .nav-link {
    color: #000;
}

.tab-nav-content {
    border-bottom: 1px solid #ddd;
}

.dropdown-menu {
    .dropdown-item {
        color: #000;
    }

    .dropdown-item {
        &:hover, &.active {
            background-color: $gray-color-light;
        }
    }
}

/*
* Context help styling
*/
.help-active {
    color: $teal !important;
}

.context-help {
    cursor: pointer;
    font-size: 1.3333rem !important;

    &.context-help-pad {
        padding-right: .6rem;
    }
}

.dash-context-help {
    i, &.dash-help {
        color: $teal;
    }
}

a {
    .dash-trash {
        color: $error-color;

        &:focus,
        &:hover,
        &:active,
        &.active {
            color: $error-color-dark;
        }
    }
}

.input-group {
    .input-group-prepend {
        margin-right: 0;
    }

    .input-group-append {
        margin-left: 0;
    }

    .input-group-btn {
        .dropdown-toggle {
            border: 2px solid #ced4da;
            border-radius: 0;
        }

        .dropdown-menu-right {
            right: 0;
            left: auto; // Reset the default from `.dropdown-menu`
        }

        .dropdown-item:hover {
            background-color: $gray-color-light;
        }
    }
}

.card {
    .card-body {
        padding: .75rem;
    }
}

.btn-toolbar {
    .btn {
        margin-right: .1rem;
    }
}

label {
    &.form-checkbox {
        cursor: pointer;
    }
}

/*
* Form validation
*/
.form-label {
    text-align: right;
    padding-right: 1rem;

    &.required {
        color: $error-color;
    }
}

.help-block.with-errors {
    width: 100%;
    color: $error-color;
    text-align: left;
    font-weight: normal;
    font-style: italic;
    display: block;
    padding-top: 3px;
}

.form-control-error, .mform-control-error {
    border-color: $error-color;
}

.tab-item {
    .menu {
        text-align: left;
        width: 100% !important;

        li {
            margin-top: 0;

            .dropdown-item {
                padding: .2rem;

                &:hover {
                    background-color: $gray-color-light;
                }
            }
        }
    }
}

.tab-content {
    .tab-pane {
        display: none;

        &.active {
            display: inherit;
        }

        .columns {
            margin-left: 0;
            margin-right: 0;
        }
    }
}

.tab-validation-error {
    color: $error-color !important;
}

.form-buttons .btn {
    margin-right: 1rem;
}

.collapsible-list {
    margin-top: 0;
    margin-bottom: 0;
    cursor: pointer;

    li {
        list-style-type: none;
        margin-top: 0;

        .form-group {
            display: inline;
        }

        label {
            cursor: pointer;
        }

        &.collapsible-list-open:before {
            content: "\e808"; /* Dash Unicode */
            font-family: "dash";
            font-size: 1.1em;
            display: inline-block;
            margin-left: -1.1em; /* same as padding-left set on li */
            width: 1.1em; /* same as padding-left set on li */
            cursor: pointer;
        }

        &.collapsible-list-closed:before {
            content: "\e807"; /* Dash Unicode */
            font-family: "dash";
            font-size: 1.1em;
            display: inline-block;
            margin-left: -1.1em; /* same as padding-left set on li */
            width: 1.1em; /* same as padding-left set on li */
            cursor: pointer;
        }
    }
}

.dash {
    display: inline-block;
    font-family: "dash";
    font-style: normal;
    font-weight: normal;
    speak: none;
    text-decoration: none;
    text-align: center;
    /* opacity: .8; */
    /* For safety - reset parent styles, that can break glyph codes*/
    font-variant: normal;
    text-transform: none;
    /* fix buttons height, for twitter bootstrap */
    line-height: 1em;
    /* You can be more comfortable with increased icons size */
    /* font-size: 120%; */
    /* Font smoothing. That was taken from TWBS */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* Uncomment for 3D effect */
    /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
    &.dash-lg {
        font-size: 1.33333333em;
        line-height: 0.75em;
    }

    &.dash-xl {
        font-size: 2em !important;
    }

    &.dash-fw {
        width: 1.3em;
        text-align: center;
    }

    &.dash-pad {
        margin-left: 1em;
    }
}

.dialog-loading {
    position: fixed;
    z-index: 99999;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 0;
    margin: 0;
    background-color: $gray-color-light;

    .loading {
        width: 100px;
        position: fixed;
        top: 50%;
        transform: translateY(-50%);
        left: 50%;
        margin: 0 -50px 0 -50px;
    }
}

select [disabled] {
    color: #808080;
}
